.card {
    background: #fff;
    border-radius: 2px;
    height: 140px;
    margin: 1rem;
    position: relative;
    width: 200px;
    -webkit-box-shadow: 0 3px 6px #999, 0 3px 6px #999;
            box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    text-align: left;
  }
  
  .card > .img-container {
    /* height: 60%; */
    overflow: hidden;
    text-align: center;
    background:#6CADDC;
  }

  .card > .img-container > img {
    width: 125%
  }
  
  


